// @flow

import styled, {createGlobalStyle} from 'styled-components'

import type {ComponentType} from 'react'

export const GlobalStyle = createGlobalStyle`
html, body, #___gatsby, #gatsby-focus-wrapper {
    height: 100%;
}

body {
    margin: 0px;
}
`

export const Container: ComponentType<{}> = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(181, 219, 146);
  cursor: none;
  flex-direction: column;
  padding-left: 14%;
  padding-right: 14%;
  padding-top: 5%;
  padding-bottom: 5%;
`

export const Ball: ComponentType<{activated?: boolean, width: number, height: number}> = styled.div`
  width: ${props => props.width}px;
  height: ${props => props.height}px;

  border-radius: 50%;
  border-color: ${props => (props.activated ? 'rgb(30, 109, 4)' : 'rgb(248,222,102)')};
  border-style: solid;
  border-width: thick;

  display: flex;
  align-items: center;
  justify-content: center;
`

export const Arc: ComponentType<{activated?: boolean}> = styled.div`
  width: 20%;
  padding-top: 20%;

  background-color: ${props => (props.activated ? 'rgb(30, 109, 4)' : 'rgb(248,222,102)')};
  border-radius: 50%;
`

export const Row: typeof styled.div = styled.div`
  flex: 1;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
`
