// @flow

import React, {useState, useEffect} from 'react'
import {ThemeProvider} from 'styled-components'
import {CssBaseline} from '@material-ui/core'
import {createMuiTheme} from '@material-ui/core/styles'

import {GlobalStyle, Container} from './styles'
import SymbolRow from './symbol-row'
import {useInterval} from '../../../../utils/common'

const theme = createMuiTheme({
  palette: {
    primary: {
      light: '#757ce8',
      main: '#3f50b5',
      dark: '#002884',
      contrastText: '#fff',
    },
    secondary: {
      light: '#ff7961',
      main: '#f44336',
      dark: '#ba000d',
      contrastText: '#000',
    },
  },
})

const RADIAN_RATE: number = 0.028
// const INTERVAL_MS: number = 170

const Se = () => {
  const [ballWidth, setBallWidth] = useState<number>(0)
  const [ballHeight, setBallHeight] = useState<number>(0)
  const [step, setStep] = useState<number>(0)
  const [delayMs, setDelayMs] = useState<?number>(250)

  useInterval(() => {
    setStep(v => {
      const newStep: number = v + 1

      if (newStep === 140) {
        setDelayMs(null)
      }

      return newStep
    })
  }, delayMs)

  const handleOnResize = () => {
    setBallWidth(window.innerWidth * RADIAN_RATE)
    setBallHeight(window.innerHeight * RADIAN_RATE)
  }

  useEffect(() => {
    setBallWidth(window.innerWidth * RADIAN_RATE)
    setBallHeight(window.innerHeight * RADIAN_RATE)
    window.addEventListener('resize', handleOnResize)

    return () => {
      window.removeEventListener('resize', handleOnResize)
    }
  }, [])

  return (
    <>
      <CssBaseline />
      <GlobalStyle />
      <ThemeProvider theme={theme}>
        <Container>
          {Array.from(Array(20)).map((_, idx) => {
            return (
              <SymbolRow
                activated={(step % 10) * 2 === idx || (step % 10) * 2 + 1 === idx}
                height={ballHeight}
                width={ballWidth}
              />
            )
          })}
        </Container>
      </ThemeProvider>
    </>
  )
}

/*
140

*/

export default Se
